.NewsSection{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    &_wrapper{
        width: 90%;
        margin-bottom: 40px;
        &_news{
            display: flex;
            justify-content: space-between; 
            width: 100%;
            flex-wrap: wrap;
         }
         &_name{
            font-family: 'Montserrat', sans-serif;
            font-weight: 500;
            font-size: 20px;
        }
        &_button{
            width: 138px;
            height: 40px;
            background: $lightOrange;
            font-family: 'Montserrat', sans-serif;
            font-weight: 500;
            font-size: 16px;
            border: none;
            border-radius: 10px;
            cursor: pointer;
            margin-top: 40px;
        }
    }
}
.Item{
    width: 270px;
    cursor: pointer;
    &_img{
        width: 270px;
        height: 208px;
        border-radius: 10px;
        object-fit: cover;
    }
    &_title{
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        font-size: 16px;
        margin-top: 20px;
    }
}

@media screen and (min-width:768px) and (max-width:1200px) {
    .NewsSection{
        display: none;
    }
}
@media screen and (min-width:320px) and (max-width:768px) {
    .NewsSection{
        display: none;
    }
}